import React from "react";
import { Flex, Heading, IconButton, Box, Text } from "@chakra-ui/react";
import { ShoppingCart } from "lucide-react";
import { useCart } from "../context/CartContext";

export default function Navbar({ onCartOpen }) {
  const { cartItems } = useCart();
  const totalCount = cartItems.reduce((sum, item) => sum + item.qty, 0);

  return (
    <Flex
      justify="space-between"
      align="center"
      bg="white"
      px={6}
      py={3}
      shadow="sm"
      position="sticky"
      top="0"
      zIndex="1000"
    >
      <Heading size="md"> Minimal Store</Heading>

      <Box position="relative">
        <IconButton
          aria-label="Cart"
          icon={<ShoppingCart />}
          onClick={onCartOpen}
          variant="ghost"
          size="lg"
        />
        {totalCount > 0 && (
          <Text
            position="absolute"
            top="1"
            right="1"
            bg="teal.400"
            color="white"
            fontSize="xs"
            fontWeight="bold"
            px="2"
            borderRadius="full"
            minW="5"
            textAlign="center"
          >
            {totalCount}
          </Text>
        )}
      </Box>
    </Flex>
  );
}
